<template>
    <li class="cart__item product">
                <!-- <li class="cart__item product" v-for="item in $store.getters.cartDetailProducts" :key="item.productId"> -->
              <div class="product__pic">
                <img :src="item.product.image" width="120" height="120" alt="item.product.title">
              </div>
              <h3 class="product__title">
                {{ item.product.title }}
              </h3>
              
              <span class="product__code">
                Артикул: {{ item.product.id }}
              </span>

              <div class="product__counter form__counter">
                <button type="button" aria-label="Убрать один товар" @click="amountMinus()">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="amount" name="count">

                <button type="button" aria-label="Добавить один товар" @click="amount = amount + 1">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <b class="product__price">
                {{ (item.amount * item.product.price) | numberFormat }} ₽
              </b>

              <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.productId)">
                <svg width="20" height="20" fill="currentColor">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </button>
            </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import { mapMutations } from 'vuex';
// import axios from 'axios';
// import {API_BASE_URL} from "../config";

export default {
    // data(){
    //         return {productAdded: false};
    //     },
    filters: {numberFormat},
    props: ['item'],
    computed: {
        amount: {
            get(){
                return this.item.amount;
            },
            set(value){
                this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value});
            },
        }
    },
    methods: {
        // ...mapActions(['deleteCartProduct']),

        ...mapMutations({deleteProduct: 'deleteCartProduct'}),
        // deleteProduct(productId){
        //     this.$store.commit('deleteCartProduct', productId);
        // }
        ...mapActions(['deleteCartProduct']),
        // ...mapMutations({deleteProduct: 'deleteCartProduct'}),
        deleteProduct(){
            this.deleteCartProduct({productId: this.item.productId, userAccessKey: this.userAccessKey})
               
        },



        // ...mapMutations({deleteProduct: 'deleteCartProduct'}),
        // deleteProduct(){
        //     this.deleteCartProduct({productId: this.item.productId, userAccessKey: this.userAccessKey})
               
        // },
        // addProductToCart(state, {productId, amount}){
        //     const item = state.cartProducts.find(item => item.productId === productId);
            
        //         item.amount += amount;
        // }
        amountMinus(){
            if(this.amount !== 1){
                this.amount = this.amount - 1
            }
        },    
        
    }
    
}
</script>