<template>
    <main class="content container" v-if="productLoading">Загрузка товара...</main>
    <main class="content container" v-else-if="!productData">Не удалось загрузить товар</main>
    <main class="content container" v-else>
			<div class="content__top">
				<ul class="breadcrumbs">
					<li class="breadcrumbs__item">
						<router-link class="breadcrumbs__link" :to="{name: 'main'}">
							Каталог
						</router-link>
					</li>
					<li class="breadcrumbs__item">
						<router-link class="breadcrumbs__link" :to="{name: 'main'}">
							{{ category.title }}
						</router-link>
					</li>
					<li class="breadcrumbs__item">
						<a class="breadcrumbs__link">
							{{ product.title }}
						</a>
					</li>
				</ul>
			</div>

			<section class="item">
				<div class="item__pics pics">
					<div class="pics__wrapper">
						<img
							width="570"
							height="570"
							:src="product.image.file.url"
							:alt="product.title"
						/>
					</div>
				
				</div>

				<div class="item__info">
					<span class="item__code">Артикул: {{ product.id }}</span>
					<h2 class="item__title">
						{{ product.title }}
					</h2>
					<div class="item__form">
						<form class="form" action="#" method="POST" @submit.prevent="addToCart">
							<b class="item__price"> {{ product.price | numberFormat }} ₽ </b>

							<fieldset class="form__block">
								<legend class="form__legend">Цвет:</legend>
								<ul class="colors">
									<li class="colors__item">
										<label class="colors__label">
											<input
												class="colors__radio sr-only"
												type="radio"
												name="color-item"
												value="blue"
												checked=""/>
											<span
												class="colors__value"
												style="
													background-color: #73b6ea;
												"
											>
											</span>
										</label>
									</li>
									<li class="colors__item">
										<label class="colors__label">
											<input
												class="colors__radio sr-only"
												type="radio"
												name="color-item"
												value="yellow"
											/>
											<span
												class="colors__value"
												style="
													background-color: #ffbe15;
												"
											>
											</span>
										</label>
									</li>
									<li class="colors__item">
										<label class="colors__label">
											<input
												class="colors__radio sr-only"
												type="radio"
												name="color-item"
												value="gray" />
											<span
												class="colors__value"
												style="
													background-color: #939393;
												"
											>
											</span
										></label>
									</li>
								</ul>
							</fieldset>

							<fieldset class="form__block">
								<legend class="form__legend">
									Объемб в ГБ:
								</legend>

								<ul class="sizes sizes--primery">
									<li class="sizes__item">
										<label class="sizes__label">
											<input
												class="sizes__radio sr-only"
												type="radio"
												name="sizes-item"
												value="32"
											/>
											<span class="sizes__value">
												32gb
											</span>
										</label>
									</li>
									<li class="sizes__item">
										<label class="sizes__label">
											<input
												class="sizes__radio sr-only"
												type="radio"
												name="sizes-item"
												value="64"
											/>
											<span class="sizes__value">
												64gb
											</span>
										</label>
									</li>
									<li class="sizes__item">
										<label class="sizes__label">
											<input
												class="sizes__radio sr-only"
												type="radio"
												name="sizes-item"
												value="128"
												checked=""/>
											<span class="sizes__value">
												128gb
											</span>
										</label>
									</li>
								</ul>
							</fieldset>

							<div class="item__row">
								<div class="form__counter">
									<button
										type="button"
										aria-label="Убрать один товар" @click="productAmountMinus()">
										<svg
											width="12"
											height="12"
											fill="currentColor"
										>
											<use xlink:href="#icon-minus"></use>
										</svg>
									</button>

									<input type="text" v-model.number="productAmount">

									<button
										type="button"
										aria-label="Добавить один товар" @click="productAmount = productAmount + 1">
										<svg
											width="12"
											height="12"
											fill="currentColor"
										>
											<use xlink:href="#icon-plus"></use>
										</svg>
									</button>
								</div>

								<button
									class="button button--primery"
									type="submit"
                                    :disabled="productAddSending"
								>
									В корзину
								</button>
							</div>

                            <div v-show="productAdded">Товар добавлен в корзину</div>
                            <div v-show="productAddSending">Добавляем товар в корзину</div>

						</form>
					</div>
				</div>

				<div class="item__desc">
					<ul class="tabs">
						<li class="tabs__item">
							<a class="tabs__link tabs__link--current">
								Описание
							</a>
						</li>
						<li class="tabs__item">
							<a class="tabs__link" href="#"> Характеристики </a>
						</li>
						<li class="tabs__item">
							<a class="tabs__link" href="#"> Гарантия </a>
						</li>
						<li class="tabs__item">
							<a class="tabs__link" href="#">
								Оплата и доставка
							</a>
						</li>
					</ul>

					<div class="item__content">
						<p>
							{{ product.title }}
						</p>

						<a href="#"> Все характеристики </a>

						<h3>Что это?</h3>

						<p>
							{{ product.title }}
						</p>

						<h3>Дизайн</h3>

						<p>
							{{ product.title }}
						</p>
					</div>
				</div>
			</section>
		</main>
</template>

<script>
    // import products from "@/data/products";
    // import categories from "@/data/categories";
    import gotoPage from "@/helpers/gotoPage";
    import numberFormat from "@/helpers/numberFormat";
    import axios from "axios"
    import {API_BASE_URL} from "../config";
    import { mapActions } from "vuex"

    export default {
        // props: ['pageParams'],
        data(){
            return {
                productAmount: 1,
                productData: null,
                productLoading: false,
                productLoadingFailed: false,

                productAdded: false,
                productAddSending: false
            };
        },
        filters: {
            numberFormat
        },
        computed: {
           product(){
            //    return products.find(product => product.id === +this.$route.params.id);
            return this.productData;
           },
           category(){
            //    return categories.find(category => category.id === this.product.categoryId);
            return this.productData.category;
           },
           products() {
                return this.productsData ? this.productsData.item.map(product => {
                    return {
                        ...product,
                        image: product.image.file.url
                    }
                }) : [];
    //   const offset = (this.page - 1) * this.productsPerPage;
    //   return this.filteredProducts.slice(offset, offset + this.productsPerPage);
            },
           
        },
        methods: {
            ...mapActions(['addProductToCart']),

            gotoPage,
            addToCart(){
                this.productAdded = false;
                this.productAddSending = true;
                this.addProductToCart({productId: this.product.id, amount: this.productAmount})
                    .then(() => {
                        this.productAdded = true;
                        this.productAddSending = false;
                    });
                // this.$store.dispatch(
                //     'addProductToCart',
                //     {productId: this.product.id, amount: this.productAmount}
                // );
            },
            productAmountMinus(){
                if(this.productAmount !== 1){
                    return this.productAmount = this.productAmount - 1
                }
                
            },
            loadProduct(){
               this.productLoading = true;
               this.productLoadingFailed = false;
               axios.get(API_BASE_URL + '/api/products/' + this.$route.params.id)
                   .then(response => this.productData = response.data)
                   .catch(() => this.productLoadingFailed = true)
                   .then(() => this.productLoading = false); 
            },

            
        },
        // created(){
        //     this.loadProduct()
        // },
        watch: {
            '$route.params.id': {
                handler(){
                    this.loadProduct();
                },
                immediate: true
            } 
        }
    }
</script>