<template>

    <main class="content container">
			<div class="content__top content__top--catalog">
				<h1 class="content__title">Каталог</h1>
				<span class="content__info"> 152 товара </span>
			</div>

			<div class="content__catalog">
				<ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :categoryId.sync="filterCategoryId"></ProductFilter>

				<section class="catalog">

                 <!-- <ProductList :products="products" @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)" /> -->
                    <div v-if="productLoading">Загрузка товаров...</div>
                    <div style="color:red"  v-if="productLoadingFalied">Произошла ошибка при загрузке товаров<button @click.prevent="loadProducts()">Попробовать ещё раз</button></div>

                    <ProductList :products="products" />
                 <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>

                </section>
			</div>
		</main>
  
</template>

<script>
// import products from '@/data/products';
import ProductList from "@/components/ProductList";
import BasePagination from "@/components/BasePagination";
import ProductFilter from "@/components/ProductFilter";
import axios from 'axios';
import {API_BASE_URL} from "../config";

export default {
//   name: "main",
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      page: 1,
      productsPerPage: 3,
      productsData: null,

      productLoading: false,
      productLoadingFalied: false
    }
  },
  computed: {
    // filteredProducts(){
    //     let filteredProducts = products;
      
    //     if(this.filterPriceFrom > 0) {
    //         filteredProducts = filteredProducts.filter(product => product.price > this.filterPriceFrom);
    //     }
    //     if(this.filterPriceTo > 0) {
    //         filteredProducts = filteredProducts.filter(product => product.price < this.filterPriceTo);
    //     }
    //     if(this.filterCategoryId) {
    //         filteredProducts = filteredProducts.filter(product => product.categoryId === this.filterCategoryId);
    //     }
    //     return filteredProducts;
    // },
    products() {
        return this.productsData ? this.productsData.items.map(product => {
            return {
                ...product,
                image: product.image.file.url
            }
        }) : [];
    //   const offset = (this.page - 1) * this.productsPerPage;
    //   return this.filteredProducts.slice(offset, offset + this.productsPerPage);
    },
    countProducts(){
        return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts(){
        this.productLoading = true;
        this.productLoadingFalied = false;
        clearTimeout(this.loadProductsTimer);
        this.loadProductsTimer = setTimeout(() => {
            axios.get(API_BASE_URL + '/api/products', {
                params: {
                    page: this.page,
                    limit: this.productsPerPage,
                    categoryId: this.filterCategoryId,
                    minPrice: this.filterPriceFrom,
                    maxPrice: this.filterPriceTo
                }
            })
            .then(response => this.productsData = response.data)
            .catch(() => this.productLoadingFalied = true)
            .then(() => this.productLoading = false);
            
        }, 0);
    }
    
  },

  watch: {
    page(){
        this.loadProducts();
    },
    filterPriceFrom(){
        this.loadProducts();
    },
    filterPriceTo(){
        this.loadProducts();
    },
    filterCategoryId(){
        this.loadProducts();
    }
  },

  created(){
        this.loadProducts();
    }
    
};
</script>
