import Vue from 'vue'
import Vuex from 'vuex'
// import products from "@/data/products"
import axios from 'axios'
import { API_BASE_URL } from "../config"
// import { mapMutations } from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		cartProducts: [
			// {productId: 1, amount: 1 }
		],

		userAccessKey: null,

        // orderId: null,

		cartProductsData: [],

        orderInfo: null
	},
	mutations: {
        updateOrderInfo(state, orderInfo){
            state.orderInfo = orderInfo;
        },
        resetCart(state){
            state.cartProducts = [];
            state.cartProductsData = [];
        },
		// ...mapMutations({ deleteProduct: "deleteCartProduct" }),
		deleteProductToCart(state, productId){
		state.cartProducts = state.cartProducts.filter(
			(item) => item.productId !== productId
		);
		// if(item){
		//     item.amount += amount;
		// }else{
		//     state.cartProducts.push({
			// 	productId,
			// 	amount,
			// });
		},

		// },
		updateCartProductAmount(state, { productId, amount }) {
			const item = state.cartProducts.find(
				(item) => item.productId === productId
			);
			if (item) {
				item.amount = amount;
			}
		},
		// deleteCartProduct(state, accessKey) {
		// 	state.cartProducts = accessKey;
		// this.productId = state.product.id;
		// },
        deleteCartProduct(state, productId) {
			state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
		},
        
		deleteCartProductRow(context, productId) {
			// state.cartProducts = state.cartProducts.filter(
			// 	(item) => item.productId !== productId
			// );
			this.state = this.state.unshift(productId);
		},
		updateUserAccessKey(state, accessKey) {
			state.userAccessKey = accessKey;
		},
		updateCartProductsData(state, items) {
			state.cartProductsData = items;
		},
		syncCartProducts(state) {
			state.cartProducts = state.cartProductsData.map((item) => {
				return {
					productId: item.product.id,
					amount: item.quantity,
				};
			});
		},
		// syncCartProductsDelete(state) {
		// 	state.cartProducts = state.cartProductsData.map((item) => {
		// 		return {
		// 			productId: item.product.id,
		// 			// amount: item.quantity,
		// 		};
		// 	});
		// },
	},
	getters: {
		cartDetailProducts(state) {
			return state.cartProducts.map((item) => {
				const product = state.cartProductsData.find(
					(p) => p.product.id === item.productId
				).product;
				return {
					...item,
					// product: products.find(p => p.id === item.productId)
					product: {
						...product,
						image: product.image.file.url,
					},
				};
			});
		},
		cartTotalPrice(state, getters) {
			return getters.cartDetailProducts.reduce(
				(acc, item) => item.product.price * item.amount + acc,
				0
			);
		},
	},
	actions: {
        loadOrderInfo(context, orderId){
            return axios
				.get(API_BASE_URL + "/api/orders/" + orderId, {
					params: {
						userAccessKey: context.state.userAccessKey,
					},
				})
                .then(response => {
                    context.commit('updateOrderInfo', response.data);
                });
        },
		loadCart(context) {
			return axios
				.get(API_BASE_URL + "/api/baskets", {
					params: {
						userAccessKey: context.state.userAccessKey,
					},
				})
				.then((response) => {
					if (!context.state.userAccessKey) {
						localStorage.setItem(
							"userAccessKey",
							response.data.user.accessKey
						);
						context.commit(
							"updateUserAccessKey",
							response.data.user.accessKey
						);
					}

					context.commit(
						"updateCartProductsData",
						response.data.items
					);
					context.commit("syncCartProducts");
				});
		},
		addProductToCart(context, { productId, amount }) {
			return axios
				.post(
					API_BASE_URL + "/api/baskets/products",
					{
						productId: productId,
						quantity: amount,
					},
					{
						params: {
							userAccessKey: context.state.userAccessKey,
						},
					}
				)
				.then((response) => {
					context.commit(
						"updateCartProductsData",
						response.data.items
					);
					context.commit("syncCartProducts");
				});
		},
		updateCartProductAmount(context, { productId, amount }) {
			context.commit("updateCartProductAmount", { productId, amount });

			if (amount < 1) {
				return;
			}

			return axios
				.put(
					API_BASE_URL + "/api/baskets/products",
					{
						productId: productId,
						quantity: amount,
					},
					{
						params: {
							userAccessKey: context.state.userAccessKey,
						},
					}
				)
				.then((response) => {
					context.commit(
						"updateCartProductsData",
						response.data.items
					);
				})
				.catch(() => {
					context.commit("syncCartProducts");
				});
		},
		deleteCartProduct(context, { productId }) {
                context.commit('deleteProductToCart', productId)
			return axios
                
				.delete(API_BASE_URL + "/api/baskets/products", {
					data: {
						productId: productId,
						// userAccessKey: context.state.userAccessKey,
					},
					params: {
						userAccessKey: context.state.userAccessKey,
					},
				})
				.then((response) => {
					context.commit("deleteCartProduct", response.data.items);
				});
			// .catch(() => {
			// 	context.commit("syncCartProducts");
			// });
		},
	},
});